import login from "../../components/login/login"
export default {
  components: {
    login
  },
  data () {
    return {
      // 用户昵称
      nickName: '',
      // 用户头像
      avatarUrl: ''
    }
  },
  methods: {
    // 打开弹窗
    open () {
      // this.$ref.loginOpen()
      this.$refs.login.loginOpen()
    },
    // // 关闭弹窗
    close () {
      this.$refs.login.loginClose()
    },
    // 去我的足迹页面
    goFootprint () {
      if (uni.getStorageSync('openid')) {
        uni.navigateTo({
          url: '/pages/footprint/footprint'
        });
      } else {
        this.$refs.login.loginOpen()
      }
    },
    // 去我的行程页面
    goTrip () {
      if (uni.getStorageSync('openid')) {
        uni.navigateTo({
          url: '/pages/trip/trip'
        });
      } else {
        this.$refs.login.loginOpen()
      }
    },
    // 去我的订单页面
    goOrderList () {
      if (uni.getStorageSync('openid')) {
        uni.navigateTo({
          url: '/pages/orderList/orderList'
        });
      } else {
        this.$refs.login.loginOpen()
      }
    },
    // 去用户协议与声明页面
    goAgreement () {
      uni.navigateTo({
        url: '/pages/agreement/agreement'
      });
    },
    // 确定退出
    signOut () {
      uni.showModal({
        content: '是否退出登录',
        confirmColor: '#59b9a6',
        success: (res) => {
          if (res.confirm) {
            uni.clearStorageSync()
            uni.showToast({
              title: '退出成功',
              duration: 2000,
              icon: 'none',
              mask: true
            });
            this.nickName = ''
            console.log(this.nickName)
          } else if (res.cancel) {
          }
        }
      })
    },
    // 去设置页面
    goSetUp () {
      uni.navigateTo({
        url: '/pages/setUp/setUp'
      });
    },
    // 分享
    goShare () {
      if (uni.getStorageSync('UserId')) {
        uni.share({
          provider: "weixin",
          title: '分享',
          href: '/pages/me/me?UserId=' + uni.getStorageSync('UserId'),
          success: res => {
            console.log('成功', res)
          }
        })
      } else {
        this.$refs.popup.open()
      }
    }
  },
  computed: {
    isnickName () {
      if (this.nickName == '') {
        return false
      }
      return true
    }
  },
  onLoad () {

  },
  onShow () {
    this.nickName = uni.getStorageSync('nickName')
    this.avatarUrl = uni.getStorageSync('avatarUrl')
    // this.$refs.popup.close()
    this.$refs.login.loginClose()
  }
}
